@charset "UTF-8";
*,
*::after,
*::before {
  background-repeat: no-repeat;
  box-sizing: inherit;
}

html {
  font-size: rem(14);
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-lato), sans-serif;
  color: #404040;
  font-weight: 400;
}

:root {
  --stagger-delay: 500ms;
}

:root {
  --font-lato: "Lato";
  --font-open: "Open Sans";
  --font-rale: "Raleway";
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

p--top-head {
  font-family: var(--font-lato);
  font-weight: 400;
  font-size: 0.8125rem;
}

/* Chỉ cho phép textarea được kéo dãn theo chiều dọc, trông sẽ đẹp hơn, không làm xấu layout */
textarea {
  resize: vertical;
}

/* Xóa bỏ khoảng trắng thừa dưới image */
img {
  vertical-align: middle;
}

/* Cũng để dễ control theo design, nên mình thường reset luôn ul, ol */
ul,
ol {
  list-style: none;
  margin: 0;
}

/* Tạo hiệu ứng bôi đen text khác biệt hơn mặc định */
::selection {
  background: #0031a2;
  color: #fff;
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateX(100%);
  }
  99% {
    opacity: 1;
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
.fade-in {
  animation: fade-in 1s ease-in-out forwards;
}

@keyframes fade-out {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  99% {
    opacity: 0;
  }
  to {
    opacity: 0;
    transform: translateX(-100%);
  }
}
.fade-out {
  animation: fade-out 1s ease-in-out forwards;
}

@keyframes top-mid {
  from {
    opacity: 0;
    transform: translateY(-100%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes left {
  from {
    opacity: 0;
    transform: translateX(-100%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes left-right {
  from {
    opacity: 0;
    transform: translateX(-100%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes right-left {
  from {
    opacity: 0;
    transform: translateX(100%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes mid {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
