.hero {
  min-height: 41.875rem;
}
.hero .container-lg {
  position: relative;
}
.hero .hero__wrapper {
  position: absolute;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  min-height: 41.875rem;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  transition: 0.4s;
}
.hero .hero__wrapper:nth-child(3) .hero__left {
  display: grid;
  place-content: center;
  text-align: center;
  color: #ffffff;
}
.hero .hero__wrapper:nth-child(3) .hero__left fieldset {
  padding: 3.875rem 6.25rem;
  border: 10px solid;
  transform: rotate(180deg);
  animation: mid 800ms ease-out backwards;
  animation-delay: calc(2 * var(--stagger-delay));
}
.hero .hero__wrapper:nth-child(3) .hero__left fieldset legend {
  font-family: var(--font-lato);
  font-weight: 800;
  padding: 0 0.625rem;
  transform: rotate(180deg);
  font-size: 4.375rem;
}
.hero .hero__wrapper:nth-child(3) .hero__left h1 {
  padding: 0;
  font-weight: 800;
  transform: matrix(2.149321267, 0, 0, 2.1462307509, 0, 0) rotate(180deg);
  animation-name: none;
}
.hero .hero__wrapper:nth-child(3) .hero__left p {
  font-family: var(--font-lato);
  font-weight: 800;
  color: red;
  transform: rotate(180deg);
  animation-name: mid;
}
.hero .hero__wrapper.active {
  opacity: 1;
  visibility: visible;
  overflow: hidden;
}
.hero .hero__wrapper.active h2 {
  animation: top-mid 800ms ease-out backwards;
  animation-delay: var(--stagger-delay);
}
.hero .hero__wrapper.active h1 {
  animation: right-left 800ms ease-out backwards;
  animation-delay: calc(2 * var(--stagger-delay));
}
.hero .hero__wrapper.active p {
  animation: left 800ms ease-out backwards;
  animation-delay: calc(3 * var(--stagger-delay));
}
.hero .hero__wrapper.active .btn {
  animation: right-left 800ms ease-out backwards;
  animation-delay: calc(4 * var(--stagger-delay));
}
.hero .hero__wrapper.active .btn--bg {
  animation: left-right 800ms ease-out backwards;
  animation-delay: calc(4 * var(--stagger-delay));
}
